@import '/src/variables.scss';

.order {
  width: 48%;
  background-color: #f7f8f7;
  border-radius: 5px;
  padding: 24px;
  &__heading {
    font-size: 20px;
    margin-bottom: 30px;
  }

  &__products {
    padding: 15px 0;
    border-bottom: 1px solid $border-color1;
    border-top: 1px solid $border-color1;
  }

  &__product {
    display: flex;
    align-items: center;
    gap: 15px;
    &:first-child {
      margin-bottom: 20px;
    }
  }

  &__productImg {
    flex-shrink: 0;
    background-color: #edebe4;
    border-radius: 5px;
    width: 64px;
    height: 64px;
    img {
      max-width: 100%;
    }
  }

  &__productDesc {
    flex-grow: 1;
  }

  &__productHeading {
    font-size: 16px;
  }

  &__productText {
    margin-top: 5px;
    font-size: 14px;
    color: #707170;
  }

  &__productPrice {
    font-weight: 700;
    font-size: 16px;
  }

  &__positions {
    padding: 20px 0;
    border-bottom: 1px solid $border-color1;
  }

  &__position {
    display: flex;
    justify-content: space-between;
    &:first-child {
      margin-bottom: 15px;
    }
  }

  &__positionItem {
    font-size: 16px;
  }

  &__total {
    display: flex;
    justify-content: space-between;
    padding: 25px 0 0;
  }

  &__totalItem {
    font-size: 18px;
    font-weight: 700;
  }
}

@media (max-width: 767.98px) {
  .order {
    width: 100%;
  }
}