@import './variables.scss';
@import './Fonts/fonts.scss';
@import "primereact/resources/themes/lara-light-cyan/theme.css";

* {
  box-sizing: border-box;
  font-family: Rubik, sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  outline: none;
  border: none;
}

.container {
  margin: 0 auto;
  max-width: 1568px;
  padding: 0 64px;
}

@media (max-width: 1439.98px) {
  .container {
    padding: 0 56px;
  }
}

@media (max-width: 1023.98px) {
  .container {
    padding: 0 40px;
  }
}

@media (max-width: 767.98px) {
  .container {
    padding: 0 24px;
  }
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.ReactCollapse--collapse {
  transition: height 500ms;
}

footer .ReactCollapse--collapse {
  transition: height 300ms;
}

main {
  flex-grow: 1;
}

body {
  @include scroll(#bac0ca, rgba(209,213,219,.6))
}

body.active, body.portal {
  overflow: hidden;
}

.cart {
  .p-float-label input:focus ~ label, .p-float-label input:-webkit-autofill ~ label, .p-float-label input.p-filled ~ label {
    top: 20px;
    font-size: 0.7em;
  }
  .p-dropdown-trigger {
    position: relative;
    top: -8px
  }
  .p-float-label .p-inputwrapper-focus ~ label, .p-float-label .p-inputwrapper-filled ~ label, .p-float-label .p-tooltip-target-wrapper ~ label {
    top: 20px;
    font-size: 0.7em;
  }
}

.p-dropdown-item {
  padding: 10px;
}
.p-highlight {
  background-color: rgb(245, 245, 245);
  color: $main-color1;
}