@import '/src/variables.scss';

.header {
  height: 80px;
  padding: 0 32px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #d9dddb;
  align-items: center;
}

.logo {
  width: 96px;
  * {
    fill: $main-color1;
  };
}
.checkout {
  display: flex;
  align-items: center;
  font-size: 16px;
  gap: 10px;
}
.lock {
  width: 12px;
}

@media (max-width: 767.98px) {
  .header {
    height: 60px;
  }
}