@import '/src/variables.scss';

.section {
  padding: 100px 0;
  background-color: #f3f1f0;
}

.text {
  color: $main-color1;
  font-size: 56px;
  text-align: center;
}

.believe {
  text-align: center;
  font-size: 24px;
  color: $main-color1;
  margin-bottom: 32px;
}

@media (max-width: 1439.98px) {
  .section {
    padding: 80px 0;
  }
  .text {
    font-size: 44px;
  }
  .believe {
    font-size: 22px;
    margin-bottom: 24px;
  }
}

@media (max-width: 1023.98px) {
  .section {
    padding: 60px 0;
  }
}

@media (max-width: 767.98px) {
  .section {
    padding: 40px 0;
  }
  .text {
    font-size: 32px;
  }
  .believe {
    font-size: 20px;
    margin-bottom: 16px;
  }
}

