@import '/src/variables.scss';

.item {
  padding: 100px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &:nth-child(2n) {
    flex-direction: row-reverse;
  }
}
.content {
  width: 43%;
}
.heading {
  font-size: 56px;
  line-height: 105%;
  font-weight: 400;
  margin-bottom: 45px;
  color: $main-color1;
}
.text {
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 35px;
  color: $main-color1;
  &:last-child {
    margin-bottom: 0;
  }
}
.img {
  position: relative;
  height: 820px;
  width: 43%;
  img {
    @include img();
  }
}

@media (max-width: 1439.98px) {
  .item {
    padding: 80px 0;
  }
  .img {
    height: 597px;
    width: 48%;
  }
  .content {
    width: 48%;
  }
}

@media (max-width: 1023.98px) {
  .item {
    padding: 60px 0;
    flex-direction: column-reverse;
    &:nth-child(2n) {
      flex-direction: column-reverse;
    }
  }
  .heading {
    font-size: 44px;
    margin-bottom: 30px;
  }
  .content {
    width: 100%;
  }
  .img {
    height: 388px;
    width: 100%;
    margin-bottom: 40px;
  }
}

@media (max-width: 767.98px) {
  .item {
    padding: 40px 0;
  }
  .img {
    height: 200px;
    margin-bottom: 40px;
  }
  .heading {
    font-size: 32px;
    margin-bottom: 20px;
  }
}