@import '/src/variables.scss';

.loader {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  border: 4px solid #cdcdcd;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  animation: rotation 1s linear infinite;
}
@keyframes rotation {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

.modal {
  * {
    color: $main-color1;
  }
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  background: hsla(120,0%,44%,.4);

  &__modal {
    text-align: center;
    padding: 30px 20px;
    background-color: white;
    border-radius: 10px;
    width: 536px;
  }

  &__heading {
    font-size: 32px;
    margin-bottom: 20px;
  }

  &__text {
    line-height: 28px;
    text-align: left;
    font-size: 18px;
    margin-bottom: 30px
  }

  &__btn {
    font-size: 20px;
    @include btn();
    padding: 10px 30px;
  }
}


.shipping {
  width: 48%;

  &__heading {
    margin-bottom: 20px;
    font-size: 20px;
  }

  &__text {
    margin-bottom: 25px;
    font-size: 12px;
  }

  &__inputBlock {
    margin-bottom: 25px;
    overflow: hidden;
    font-size: 18px;
  }

  &__input {
    width: 100%;
    border: 1px solid $border-color1;
    padding: 25px 12px 8px;
    font-size: 1em;
    box-shadow: none;
  }

  &__label {
    white-space: nowrap;
    font-size: 18px;
    z-index: 1;
    margin-top: -10px;
  }
  &__sub {
    margin-bottom: 25px;
    padding-left: 30px;
    position: relative;
  }
  &__icon {
    opacity: 0;
    background-color: $main-color1;
    border-radius: 3px;
    width: 20px;
    position: absolute;
    top: 2px;
    left: 0;
    z-index: 1;
    * {
      color: white;
    }
  }
  &__subInput {
    opacity: 0;
    width: 20px;
    height: 20px;
    position: absolute;
    top: 2px;
    z-index: 2;
    left: 0;
  }
  &__subInput:checked + &__icon {
    opacity: 1;
  }

  &__subLabel {
    font-size: 16px;
    &::before {
      box-sizing: border-box;
      top: 2px;
      left: 0;
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      border-radius: 3px;
      border: 2px solid $border-color1;
    }
  }

  &__btn {
    background-color: #0c3d3d;
    color: #fff;
    border-radius: 32px;
    padding: 16px;
    width: 100%;
    font-size: 20px;
    cursor: pointer;
    transition: 0.3s ease;
    margin-top: 20px;
    &:hover {
      color: #000;
      background-color: #46de46;
    }
    @media (hover: none) {
      &:hover {
        background-color: #0c3d3d;
        color: #fff;
      }
    }
  }
  &__btnLoad {
    cursor: auto;
    &:hover {
      background-color: #0c3d3d;
      color: #fff;
    } 
  }
  &__error {
    color: #ff514b;
    font-size: 14px;
    margin-top: 8px;
  }
  &__inputError {
    border: 1px solid #ff514b;
  }
}

@media (max-width: 767.98px) {
  .shipping {
    padding: 0 20px;
    width: 100%;
    margin-bottom: 40px;
    &__btn {
      font-size: 18px;
    }
    &__inputBlock {
      margin-bottom: 15px;
    }
    &__sub {
      margin-bottom: 15px;
    }
  }
}