@import '/src/variables.scss';

.section {
  background-color: #f3f1f0;
  padding: 100px 0;
}
.heading {
  font-size: 56px;
  margin-bottom: 50px;
  font-weight: 400;
}
.items {
  display: flex;
  justify-content: space-between;
}
.item {
  width: 31.5%;
}
.img {
  margin-bottom: 25px;
  position: relative;
  width: 100%;
  height: 296px;
  img {
    @include img();
  }
}
.content {
  
}
.itemHeading {
  font-size: 24px;
  color: $main-color1;
  margin-bottom: 25px;
}
.text {
  color: $main-color1;
  font-size: 18px;
  line-height: 28px;
}

@media (max-width: 1439.98px) {
  .section {
    padding: 80px 0;
  }
  .heading {
    font-size: 44px;
  }
  .items {
    flex-direction: column;
  }
  .item {
    width: 100%;
    display: flex;
    gap: 30px;
    margin-bottom: 50px;
    align-items: center;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .img {
    flex-shrink: 0;
    width: 280px;
    height: 280px;
    margin-bottom: 0;
  }
}

@media (max-width: 1023.98px) {
  .section {
    padding: 60px 0;
  }
  .img {
    width: 205px;
    height: 205px;
  }
}

@media (max-width: 767.98px) {
  .section {
    padding: 40px 0;
  }
  .heading {
    font-size: 32px;
  }
  .item {
    gap: 15px;
    flex-direction: column;
    margin-bottom: 20px;
  }
  .itemHeading {
    font-size: 20px;
  }
  .img {
    width: 100%;
    height: 228px;
  }
}