@import '/src/variables.scss';

.container {
  max-width: 1256px;
  margin: 0 auto;
  padding: 44px 56px 72px;
  display: flex;
  justify-content: space-between;
  align-items: start;
}

.mobile {
  display: none;
}


@media (max-width: 1023.98px) {
  .container {
    padding: 44px 56px 72px;
  }
}

@media (max-width: 767.98px) {
  .container {
    padding: 0;
    flex-direction: column;
  }
  .mobile {
    width: 100%;
    padding: 0 20px 30px;
    display: block;
    
    &__img {
      text-align: center;
      img {
        max-width: 180px;
      }
    }
    &__heading {
      text-align: center;
      font-size: 18px;
      color: $main-color1;
      margin-bottom: 15px;
    }
    &__price {
      text-align: center;
      margin-bottom: 15px;
      font-size: 18px;
      font-weight: 700;
    }
    
    &__list {
      display: flex;
      flex-direction: column;
      align-items: start;
      max-width: 300px;
      margin: 0 auto;
      li {
        font-size: 12px;
        padding-left: 28px;
        font-weight: 700;
        color: #6b7280;
        position: relative;
        display: inline-block;
        margin: 0 0 7px;
        &::before {
          position: absolute;
          content: "";
          width: 16px;
          height: 16px;
          top: 0;
          left: 0;
          background: no-repeat 50%/16px 16px url('./img/icon.svg');
        }
      }
    }

  }
}