.inputBlock {
  background-color: white;
  border-radius: 32px;
  padding: 12px 16px;
  display: flex;
  align-items: center;
  width: 270px;
  position: relative;
  overflow-x: hidden;
}
.error {
  width: 270px;
  margin-top: 5px;
  font-size: 12px;
}
.input {
  font-size: 16px;
  color: #0f2e2f;
}
.success {
  color: #2f7d39;
  font-size: 16px;
  position: absolute;
  background: white;
  width: 100%;
  left: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inputError {
  color: #cd3f3f;
}
.btn {
  cursor: pointer;
  background-color: transparent;
  height: 24px;
  img {
    width: 24px;
  }
}
.loader {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  border: 4px solid #0f2e2f;
  border-bottom-color: #cdcdcd;
  border-radius: 50%;
  display: inline-block;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
} 
@media (max-width: 1439.98px) {
  .input {
    width: 100%;
  }
  .inputBlock {
    width: 362px;
  }
  .error {
    width: 362px;
  }
}

@media (max-width: 1023.98px) {
  .inputBlock {
    width: 60vw;
  }
  .error {
    width: 60vw;
  }
}

@media (max-width: 767.98px) {
  .inputBlock {
    width: 100%;
  }
  .error {
    width: 100%;
  }
}