@import '/src/variables.scss';

.top{
  padding: 100px 0;
		&__sustain {
			font-size: 24px;
			color: $main-color1;
		}

		&__heading {
			font-size: 56px;
			font-weight: normal;
			margin-bottom: 50px;
		}

		&__wrap {
			align-items: center;
			justify-content: space-between;
			display: flex;
		}

		&__img {
			position: relative;
			height: 789px;
			width: 48%;
			img {
				@include img();
			}
		}

		&__content {
			width: 44%;
		}

		&__contentBlock {
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0;
			}
		}

		&__contentHeading {
			font-size: 20px;
			color: $main-color1;
			margin-bottom: 10px;
		}

		&__contentText {
			line-height: 28px;
			font-size: 18px;
			color: $main-color1;
			a {
				text-decoration: underline;
				color: $main-color1;
			}
		}
}

.bottom{
  padding: 100px 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
		&__content {
			width: 49%;
		}

		&__heading {
			font-size: 56px;
			font-weight: 400;
			margin-bottom: 40px;
		}

		&__text {
			font-size: 18px;
			line-height: 28px;
		}

		&__img {
			width: 43%;
			height: 717px;
			position: relative;
			img {
				@include img();
			}
		}
}

@media (max-width: 1439.98px) {
  .top, .bottom {
    padding: 80px 0;
  }
	.top {
		&__sustain {
			font-size: 22px;
		}
		&__heading {
			font-size: 44px;
		}
		&__content {
			width: 47%;
		}
	}
	.bottom {
		&__content {
			width: 48%;
		}
		&__img {
			width: 48%;
			height: 597px;
		}
		&__heading {
			font-size: 44px;
		}
	}
}

@media (max-width: 1023.98px) {
  .top, .bottom {
    padding: 60px 0;
  }
	.top {
		&__wrap {
			flex-direction: column;
		}
		&__content {
			width: 100%;
		}
		&__img {
			width: 100%;
			height: 450px;
			margin-bottom: 30px;
		}
	}
	.bottom {
		flex-direction: column-reverse;
		&__content {
			width: 100%;
		}
		&__img {
			width: 100%;
			margin-bottom: 35px;
			height: 388px;
		}
		&__heading {
			margin-bottom: 20px;
		}
	}
}

@media (max-width: 767.98px) {
  .top, .bottom {
    padding: 40px 0;
  }
	.top {
		&__sustain {
			font-size: 20px;
			text-align: center;
		}
		&__heading {
			font-size: 32px;
			text-align: center;
			margin-bottom: 30px;
		}
		&__img {
			height: 375px;
		}
		&__contentBlock {
			border-bottom: 1px solid #bac0ca;
			margin-bottom: 0;
		}
		&__contentHeading {
			font-size: 18px;
			padding: 20px 0;
			margin-bottom: 0;
			position: relative;
			width: calc(100% - 20px);
			&::before,&::after {
				position: absolute;
				content: '';
				width: 2px;
				height: 16px;
				background-color: #2f7d39;
				top: 28px;
				right: -18px;
			}
			&::before {
				transform: rotate(90deg);
			}
		}
		&__contentText {
			padding-bottom: 30px;
		}
	}
	.bottom {
		&__img {
			height: 200px;
		}
		&__heading {
			font-size: 32px;
		}
	}
}