@import '/src/variables.scss';

.section {
  position: relative;
  padding: 0px 20px;
  height: 617px;
  display: flex;
  align-items: center;
}
.imgWrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.img {
  width: 100%;
  position: relative;
  height: 617px;
  img {
    @include img();
  }
}
.heading {
  font-weight: 400;
  font-family: 'Times New Roman', serif;
  position: relative;
  z-index: 1;
  color: white;
  font-size: 72px;
  max-width: 1108px;
  margin: 0 auto;
  text-align: center;
}

@media (max-width: 1439.98px) {
  .section {
    padding: 0px 20px;
    height: 525px;
  }
  .heading {
    max-width: 902px;
  }
  .img {
    height: 525px;
  }
}

@media (max-width: 1023.98px) {
  .section {
    padding: 0px 20px;
    height: 480px;
  }
  .heading {
    max-width: 688px;
    font-size: 56px;
  }
  .img {
    height: 480px;
  }
}

@media (max-width: 767.98px) {
  .section {
    padding: 0 20px;
    height: 472px;
  }
  .heading {
    max-width: 327px;
    font-size: 40px;
  }
  .img {
    height: 472px;
  }
}

@media (max-width: 567.98px) {
}